*,
*::before,
*::after {
  // removes gray tap background in iOS
  -webkit-tap-highlight-color: rgba($color--white, 0);
  box-sizing: border-box;
}

a {
  -webkit-text-decoration-skip-ink: auto;
  text-decoration-skip-ink: auto;

  main & {
    color: $color--rose-of-sharon;
    font-weight: $font-weight--bold;
  }

  &:active {
    background-color: transparent; // disables gray background on click in IE
  }
}

a,
button,
input,
select,
textarea {
  @include a11y-focus;
}

abbr {
  cursor: help;
}

body {
  background-color: $color--white;
  color: $color--tuatara;
  font: 1.4rem/1.75 $font-family--sans;
  font-weight: $font-weight--normal;
  margin: 0;
  padding: 0;

  @include respond-to('medium-large') {
    font-size: 1.6rem;
  }

  // allows click events on the body to work in iOS
  [data-whatinput='touch'] & {
    -webkit-tap-highlight-color: rgba($color--white, 0);
    cursor: pointer;
  }
}

div,
form,
main {
  &[tabindex='-1'] {
    // remove focus outline on containers
    // that have a negative tabindex
    outline: none;
  }
}

blockquote,
dl,
hr,
ol,
p,
ul {
  margin: unit(1) 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: unit(2) 0 unit(1);
}

blockquote {
  @include null-margins;
}

em {
  font-style: italic;
}

html {
  // set base font size to 10px
  font-size: 62.5%;
}

img {
  -ms-interpolation-mode: bicubic;
  border: none;
  image-rendering: optimizeQuality;
}

pre {
  font-family: monospace;
}

strong {
  font-weight: $font-weight--bold;
}

ol {
  counter-reset: li;
  list-style: none;

  li {
    counter-increment: li;
    position: relative;

    &::before {
      color: $color--venice-blue;
      content: counter(li);
      display: inline-block;
      font-size: 1.8rem;
      font-weight: $font-weight--bold;
      left: -2rem;
      line-height: 1.5;
      min-width: unit(1);
      padding-right: unit(0.5);
      position: absolute;
      top: 0;
    }
  }
}

li + li {
  margin-top: unit(1);
}

.external-link:not(.no-external-link-icon):not(.social-share__link):not(.bio-list__link__a):not(.home-social__links__item__link) {
  background-image: url(../images/icon-external-link--orange.png);
  background-position: calc(100% - 2px) center;
  background-repeat: no-repeat;
  background-size: 14px 14px;
  padding-right: 20px;
}

.two-column-list__item .external-link {
  background-image: url(../images/icon-external-link--blue.png) !important;
  background-position: calc(100% - 2px) center !important;
}

.-dark-gray .external-link {
  background-image: url(../images/icon-external-link--white.png) !important;
  background-position: calc(100% - 2px) center !important;
}
